*, :before, :after {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

a {
  color: #000;
  text-decoration: none;
}

ul, ol, li {
  padding-left: 0;
  list-style: none;
}

img {
  vertical-align: top;
}

html {
  --blue: #00acdc;
}

body {
  height: 100%;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.container {
  margin: 0 auto;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  text-transform: uppercase;
  display: flex;
}

.nav__item {
  display: flex;
  position: relative;
}

.nav__link {
  align-items: center;
  padding: 23px 0;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover {
  color: var(--blue);
}

.nav__link:hover:after {
  display: block;
}

.is-active {
  color: var(--blue);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
